"use client";

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface SensorData {
  temperature: number;
  movement: string;
  feedLevel: string;
  waterLevel: string;
  heartRate?: number;
  activityLevel?: number;
  analysis: {
    feedStatus: string;
    waterStatus: string;
    healthStatus: string;
    diseaseIndicator?: string;
    riskLevel: 'Low' | 'Medium' | 'High';
    recommendations: string[];
  };
  timestamp: number;
}

export default function SensorMonitoringPage() {
  const [sensorData, setSensorData] = useState<SensorData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [autoRefresh, setAutoRefresh] = useState(true);

  const fetchSensorData = async () => {
    try {
      setError(null);
      const response = await fetch('/api/sensor');
      const result = await response.json();
      
      if (result.success) {
        setSensorData(result.data);
      } else {
        setError(result.error || 'Failed to fetch sensor data');
      }
    } catch (err) {
      setError('Network error: Unable to fetch sensor data');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchSensorData();
  }, []);

  useEffect(() => {
    if (autoRefresh) {
      const interval = setInterval(fetchSensorData, 5000); // Refresh every 5 seconds
      return () => clearInterval(interval);
    }
  }, [autoRefresh]);

  const getHealthStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'healthy': return 'bg-green-500';
      case 'at risk': return 'bg-yellow-500';
      case 'sick': return 'bg-orange-500';
      case 'critical': return 'bg-red-500';
      default: return 'bg-gray-500';
    }
  };

  const getRiskLevelColor = (level: string) => {
    switch (level.toLowerCase()) {
      case 'low': return 'bg-green-100 text-green-800 border-green-200';
      case 'medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'high': return 'bg-red-100 text-red-800 border-red-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  const getTemperatureStatus = (temp: number) => {
    if (temp < 36) return { status: 'Low', color: 'text-blue-600' };
    if (temp > 41) return { status: 'Critical', color: 'text-red-600' };
    if (temp > 39.5) return { status: 'High', color: 'text-orange-600' };
    return { status: 'Normal', color: 'text-green-600' };
  };

  const getActivityProgress = (level?: number) => {
    return level || 0;
  };

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex items-center justify-center min-h-96">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Loading sensor data...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Alert className="max-w-2xl mx-auto">
          <AlertDescription className="text-center">
            <p className="mb-4">{error}</p>
            <Button onClick={fetchSensorData}>Retry</Button>
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  if (!sensorData) {
    return (
      <div className="container mx-auto px-4 py-8">
        <p className="text-center text-muted-foreground">No sensor data available</p>
      </div>
    );
  }

  const tempStatus = getTemperatureStatus(sensorData.temperature);

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Live Sensor Dashboard</h1>
          <p className="text-muted-foreground">
            Last updated: {new Date(sensorData.timestamp).toLocaleString()}
          </p>
        </div>
        <div className="flex gap-2 mt-4 sm:mt-0">
          <Button 
            variant={autoRefresh ? "default" : "outline"}
            onClick={() => setAutoRefresh(!autoRefresh)}
          >
            {autoRefresh ? 'Auto-Refresh ON' : 'Auto-Refresh OFF'}
          </Button>
          <Button onClick={fetchSensorData} disabled={loading}>
            Refresh Now
          </Button>
        </div>
      </div>

      {/* Health Status Alert */}
      {sensorData.analysis.healthStatus !== 'Healthy' && (
        <Alert className="mb-6 border-l-4 border-l-orange-500">
          <AlertDescription>
            <div className="flex items-center justify-between">
              <div>
                <strong>Health Alert:</strong> {sensorData.analysis.healthStatus}
                {sensorData.analysis.diseaseIndicator && (
                  <span className="block text-sm mt-1">{sensorData.analysis.diseaseIndicator}</span>
                )}
              </div>
              <Badge className={getRiskLevelColor(sensorData.analysis.riskLevel)}>
                {sensorData.analysis.riskLevel} Risk
              </Badge>
            </div>
          </AlertDescription>
        </Alert>
      )}

      {/* Main Metrics Grid */}
      <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {/* Temperature Card */}
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Temperature</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{sensorData.temperature.toFixed(1)}Â°C</div>
            <p className={`text-sm ${tempStatus.color}`}>{tempStatus.status}</p>
          </CardContent>
        </Card>

        {/* Movement Card */}
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Movement</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold capitalize">{sensorData.movement}</div>
            <p className="text-sm text-muted-foreground">Current activity</p>
          </CardContent>
        </Card>

        {/* Feed Level Card */}
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Feed Level</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{sensorData.feedLevel}</div>
            <p className="text-sm text-muted-foreground">Status: {sensorData.analysis.feedStatus}</p>
          </CardContent>
        </Card>

        {/* Water Level Card */}
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Water Level</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{sensorData.waterLevel}</div>
            <p className="text-sm text-muted-foreground">Status: {sensorData.analysis.waterStatus}</p>
          </CardContent>
        </Card>
      </div>

      {/* Detailed Analysis Grid */}
      <div className="grid lg:grid-cols-2 gap-6 mb-8">
        {/* Health Analysis Card */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              Health Analysis
              <div className={`w-3 h-3 rounded-full ${getHealthStatusColor(sensorData.analysis.healthStatus)}`}></div>
            </CardTitle>
            <CardDescription>AI-powered health assessment</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm font-medium">Health Status</span>
                <Badge className={getRiskLevelColor(sensorData.analysis.riskLevel)}>
                  {sensorData.analysis.riskLevel} Risk
                </Badge>
              </div>
              <p className="text-lg font-semibold">{sensorData.analysis.healthStatus}</p>
              {sensorData.analysis.diseaseIndicator && (
                <p className="text-sm text-muted-foreground mt-1">
                  {sensorData.analysis.diseaseIndicator}
                </p>
              )}
            </div>

            {sensorData.heartRate && (
              <div>
                <span className="text-sm font-medium">Heart Rate</span>
                <p className="text-lg font-semibold">{sensorData.heartRate.toFixed(0)} BPM</p>
              </div>
            )}

            {sensorData.activityLevel !== undefined && (
              <div>
                <div className="flex justify-between items-center mb-2">
                  <span className="text-sm font-medium">Activity Level</span>
                  <span className="text-sm text-muted-foreground">{sensorData.activityLevel.toFixed(0)}%</span>
                </div>
                <Progress value={getActivityProgress(sensorData.activityLevel)} className="h-2" />
              </div>
            )}
          </CardContent>
        </Card>

        {/* Recommendations Card */}
        <Card>
          <CardHeader>
            <CardTitle>AI Recommendations</CardTitle>
            <CardDescription>Automated care suggestions</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {sensorData.analysis.recommendations.map((recommendation, index) => (
                <div key={index} className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary rounded-full mt-2 flex-shrink-0"></div>
                  <p className="text-sm">{recommendation}</p>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
          <CardDescription>Common farm management tasks</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-wrap gap-2">
            <Button variant="outline" size="sm">Record Feeding</Button>
            <Button variant="outline" size="sm">Update Water</Button>
            <Button variant="outline" size="sm">Schedule Vet Visit</Button>
            <Button variant="outline" size="sm">Add Notes</Button>
            <Button variant="outline" size="sm">Export Data</Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
